import { Component, OnInit } from '@angular/core';
import { CategoriesService, GlobalService, ProductsService } from '../services';

@Component({
  selector: 'app-fruits',
  templateUrl: './fruits.component.html',
  styleUrls: ['./fruits.component.css']
})
export class FruitsComponent implements OnInit {
  products = [];
  productCount = 0;

  constructor(private category: CategoriesService,
              private global: GlobalService,
              private product: ProductsService) { }

  ngOnInit(): void {
    this.getProducts();
    this.global.cartProduct.next(JSON.parse(localStorage.getItem('cart')));
  }


  getProducts() {
    const param = {
      isActive: true
    };
    this.product.getAllProducts(param).subscribe(res => {
      this.products = res.data;
    });
  }

  addToCart(product){
    product.isShowCount = true;
    product.count = 1;
    product.amount = (product.productPrice * product.count);
    this.global.addTocart(product);
  }

  addItems(product){
    product.count ++;
    product.amount = (product.productPrice * product.count);
    this.global.addTocart(product);
  }
  removeItems(product){
    product.count --;
    product.amount = (product.productPrice * product.count);
    this.global.addTocart(product);
  }
}
