import { Injectable } from '@angular/core';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';
import { BehaviorSubject, from } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class GlobalService {
  totalPrice = 0;
  totalItems = 0;

  cartProduct = new BehaviorSubject([]);
  cart = this.cartProduct.asObservable();

  constructor(private route: Router) { }

  getcurrentUser(){
    if (localStorage.getItem('user') !== 'undefined' && localStorage.getItem('user') !== '') {
        return JSON.parse(localStorage.getItem('user'));
    }
    return null;
  }

  successAlert(message){
    Swal.fire(message);
  }

  errorAlert(message){
    Swal.fire(message, 'error');
  }

  redirect(link) {
    this.route.navigate(['/' + link]);
  }

  logout(){
    localStorage.clear();
    this.redirect('');
    location.reload();
  }

  addTocart(product){
     const getCart = JSON.parse(localStorage.getItem('cart'));
     let products = [];
     const addItem = {
      totalPrice: 0,
      totalItems: 0,
      products: '',
      id: ''
     };
     if (!getCart){
      addItem.totalPrice =  (product.productPrice * product.count);
      addItem.totalItems ++;
      addItem.id = product._id;
      addItem.products = product;
      products.push(addItem);
    } else {
        let isNewItem = true;
        for (const i of getCart){
             if (i.id === product._id){
              isNewItem = false;
              i.totalPrice = i.totalPrice + product.productPrice;
              i.totalItems ++;
             }
        }
        if (isNewItem){
          addItem.totalPrice =  (product.productPrice * product.count);
          addItem.totalItems ++;
          addItem.id = product._id;
          addItem.products = product;
          getCart.push(addItem);
        }
        products = getCart;
    }

     localStorage.setItem('cart', JSON.stringify(products));
     this.cartProduct.next(products);
  }
}
