import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { FruitsComponent } from './fruits/fruits.component';
import { ViewDetailComponent } from './view-detail/view-detail.component';
import { DashbordComponent } from './dashbord/dashbord.component';
import { ProductsComponent } from './products/products.component';
import { CategoryComponent } from './category/category.component';
import { AdminGuard } from './_helper/admin.guard';
import { CartComponent } from './cart/cart.component';


const routes: Routes = [
  {path: '', component: HomeComponent, children: [
    {path: '', component: FruitsComponent},
    {path: 'view-detail/:id', component: ViewDetailComponent},
    {path: 'cart', component: CartComponent}
  ]},
  {path: 'dashbord', component: DashbordComponent, children: [
    {path: 'add-products', component: ProductsComponent},
    { path: '', redirectTo: 'add-products', pathMatch: 'full' },
    {path: 'add-category', component: CategoryComponent}
  ]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
